.search_page {
  padding: 15px 30px 0;
  transition: .5s;
}
.filter_search {
  padding: 0 10px;
  max-width: 1066px;
}
.filter_head div {
  display: flex;
  width: fit-content;
  align-items: center;
  font-size: 17px;
  font-weight: 600;
  border-radius: 100px;
  padding: 5px 12px;
  cursor: pointer;
}
.filter_head div:hover {
  background-color: #4d4a4a66;
}
.filter_head svg {
  margin-right: 10px;
  font-size: 25px;
}
.filters {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 20px;
  border-bottom: 1px solid #4d4a4a66;
  height: 0;
  overflow: hidden;
  margin-top: 0;
  transition: .5s;
}
.filters.active {
  transition: .5s;
  height: 280px;
}
.filters > div h4 {
  font-size: 15px;
  font-weight: 500;
  padding: 10px 0;
  border-bottom: 1px solid #4d4a4a66;
  margin-bottom: 10px;
}
.filters > div li {
  font-size: 14px;
  padding: 5px 0;
  color: #aaa;
  cursor: pointer;
  width: fit-content;
}
ul li {
  list-style: none;
}
.res_display {
  margin: 20px 0 10px;
  padding: 0 10px;
  max-width: 1066px;
}
@media (max-width: 550px) {
  .search_page {
    padding: 15px 10px 0;
  }
}
@media (max-width: 450px) {
  .filters {
   overflow-x: scroll;
  }
}
